import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Base } from '../../../model/base';
import { StatusItemInspecao} from '../../../model/objetc/statusiteminspecao';
import { StatusitensinspecaoService } from './../../../service/sigobra/statusitensinspecao.service';


@Component({
  selector: 'app-statusitensinspecoes',
  templateUrl: './statusitensinspecoes.page.html',
  styleUrls: ['./statusitensinspecoes.page.scss'],
})
export class StatusitensinspecoesPage implements OnInit {

  
  statusitens: Array<StatusItemInspecao>;
  status: StatusItemInspecao;
  constructor(private base: Base, private siis: StatusitensinspecaoService, private router: Router) { }

  ngOnInit() {
    this.carregarstatus(" ");    
  }
  excluir(status : StatusItemInspecao) {
    this.base.Confirma('Deseja excluir essa status', this.removerativo.bind(this), status);
  }

  remover(status : StatusItemInspecao) {
    this.base.present();   
    return this.siis.delete(status).subscribe(
      data => {
        this.base.dismiss();
        this.base.menssagemSucesso('Status Excluido');
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao Excluir  status '+ status.id + this.base.tratarErro(error));  
      }
    );
  }



  editar(status : StatusItemInspecao) { 
    this.siis.status = status;
    this.router.navigate(['statusitensinspecao-inserir']);   
  }
  
  
  carregarstatus(value: string) {
    return this.siis.pesquisar(value).subscribe(
      data => {
        this.statusitens = data as Array<StatusItemInspecao>;
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao listar  status ' + this.base.tratarErro(error));
      }
    );
  }

  inserir() {
    this.siis.status = new StatusItemInspecao;
    this.router.navigate(['statusitensinspecao-inserir']);
  }

  removerativo(status : StatusItemInspecao) {
    this.status= status;
    this.base.present();  
    if ( (this.status.ativo !== null)&& (this.status.ativo !== undefined)){      
      this.status.ativo = false;
    }  
    return this.siis.editarativo(this.status).subscribe(
      data => {        
        this.base.dismiss();
        this.router.navigate(['statusitensinspecoes']);
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao Excluir  status '+ this.status.id + this.status.ativo 
        + this.base.tratarErro(error));  
      }
    );           
  }
}
